body {
  margin: 0;
  padding: 0;
}

.container {
  position: relative;
  max-width: 650px;
  min-width: 360px;
  height: 100%;
  margin: 0 auto;
  /* background-color: red; */
}

.header {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
}

.logo {
  height: 15px;
}

.bg {
  height: 85vh;
  width: 100%;
  background-image: linear-gradient(
      to bottom,
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.1),
      rgba(255, 255, 255, 0.2),
      rgba(255, 255, 255, 0.3),
      rgba(255, 255, 255, 0.4),
      rgba(255, 255, 255, 0.5),
      rgba(255, 255, 255, 0.8),
      rgba(255, 255, 255, 0.9),
      rgba(255, 255, 255, 1)
    ),
    url("./../images/bg/bg1.jpg");
}

.nav {
  width: 350px;
  height: 10px;
  margin: 0 auto;
  /* background-color: royalblue; */
  display: flex;
  justify-content: space-evenly;
  margin-bottom: 12px;
}

.nav div {
  width: 60px;
  height: 8px;
  border-radius: 5px;
  background-color: #00193533;
}

.nav div:first-child {
  background-color: #001935;
}

.author {
  float: right;
  margin-right: 30px;
  font-size: 14px;
}

.space {
  height: 90%;
}

.space:last-child {
  height: 5px;
}

.content {
  color: #000d1b99;
  position: absolute;
  bottom: 130px;
  width: 100%;
  text-align: center;
}

.btn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  position: absolute;
  height: 120px;
  bottom: 0;
}

a {
  width: 240px;
  height: 48px;
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 24px;
  color: #fff;
  background-color: #001935;
  box-shadow: 6px 6px 6px 6px #ffffff52;
  font-size: 16px;
}
